<template>
    <div id="view-project">
        <ul class="collection with-header">
            <li class="collection-header"><h4> : {{project_id}}</h4></li>
            <li class="collection-header"><h4>: {{project_desc}}</h4></li>
            <li class="collection-header"><h4> : {{project_info}}</h4></li>
            <li class="collection-header"><h4>: {{residual}}</h4></li>
            <li class="collection-header"><h4> : {{to_start}}</h4></li>
            <li class="collection-header"><h4> : {{to_end}}</h4></li>
            <li class="collection-header"><h3> </h3></li>
            <li class="collection-header"><h4>: {{section_name}}</h4></li>
            <li class="collection-header"><h4> : {{contract_value}}</h4></li>
            <li class="collection-header"><h4>  30/6/2020: {{approved_30_6_20}}</h4></li>
            <li class="collection-header"><h4> 2020/2021: {{approved_20_21}}</h4></li>
            <li class="collection-header"><h4>  30/6/2021: {{total_approved_2021}}</h4></li>
            <li v-if="company_name" class="collection-header"><h4> : {{company_name}}</h4></li>
            <li v-if="outgoing" class="collection-header"><h4>: {{outgoing}}</h4></li>
            <li v-if="execution_rate" class="collection-header"><h4>  : {{execution_rate}}</h4></li>
            <li v-if="company_name_1" class="collection-header"><h4> : {{company_name_1}}</h4></li>
            <li v-if="outgoing_1" class="collection-header"><h4>: {{outgoing_1}}</h4></li>
            <li v-if="execution_rate_1" class="collection-header"><h4>  : {{execution_rate_1}}</h4></li>
            <li v-if="section_name_1" class="collection-header"><h3> </h3></li>
            <li v-if="section_name_1" class="collection-header"><h4>: {{section_name_1}}</h4></li>
            <li v-if="contract_value_1" class="collection-header"><h4>  : {{contract_value_1}}</h4></li>
            <li v-if="first_approved_30_6_20" class="collection-header"><h4>  30/6/2020: {{first_approved_30_6_20}}</h4></li>
            <li v-if="first_approved_20_21" class="collection-header"><h4> 2020/2021: {{first_approved_20_21}}</h4></li>
            <li v-if="first_total_approved_2021" class="collection-header"><h4>  30/6/2021: {{first_total_approved_2021}}</h4></li>
            <li v-if="company_name_2" class="collection-header"><h4> : {{company_name_2}}</h4></li>
            <li v-if="outgoing_2" class="collection-header"><h4>: {{outgoing_2}}</h4></li>
            <li v-if="execution_rate_2" class="collection-header"><h4>  : {{execution_rate_2}}</h4></li>
            <li v-if="company_name_3" class="collection-header"><h4> : {{company_name_3}}</h4></li>
            <li v-if="outgoing_3" class="collection-header"><h4>: {{outgoing_3}}</h4></li>
            <li v-if="execution_rate_3" class="collection-header"><h4>  : {{execution_rate_3}}</h4></li>
        </ul>
        <router-link to="/" class="btn grey right">Back</router-link>
        <button @click="deleteProject" class="btn red">Delete</button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-project', params: {project_id: project_id}}" class="btn-floating btn-large red">
                
            </router-link>
        </div>

    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-project',
    data() {
        return {
            project_id: null,
            project_desc: null,
            project_info: null,
            residual: null,
            to_start: null,
            to_end: null,
            section_name: null,
            section_name_1: null,
            contract_value: null,
            contract_value_1: null,
            approved_30_6_20: null,
            first_approved_30_6_20: null,
            approved_20_21: null,
            first_approved_20_21: null,
            total_approved_2021: null,
            first_total_approved_2021: null,
            company_name: null,
            company_name_1: null,
            company_name_2: null,
            company_name_3: null,
            outgoing: null,
            outgoing_1: null,
            outgoing_2: null,
            outgoing_3: null,
            execution_rate: null,
            execution_rate_1: null,
            execution_rate_2: null,
            execution_rate_3: null,
            contract_value: null,
        }
    },
    beforeRouteEnter(to, from, next) {
        db.collection('projects').where('project_id', '==', to.params.project_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.project_id = doc.data().project_id
                    vm.project_desc = doc.data().project_desc
                    vm.project_info = doc.data().project_info
                    vm.residual = doc.data().residual
                    vm.to_start = doc.data().to_start
                    vm.to_end = doc.data().to_end
                    vm.section_name = doc.data().sections[0].section_name
                    vm.contract_value = doc.data().sections[0].contract_value
                    vm.approved_30_6_20 = doc.data().sections[0].approved_30_6_20
                    vm.approved_20_21 = doc.data().sections[0].approved_20_21
                    vm.total_approved_2021 = doc.data().sections[0].total_approved_2021

                    if (doc.data().sections[1]) {
                        vm.section_name_1 = doc.data().sections[1].section_name
                        vm.contract_value_1 = doc.data().sections[1].contract_value
                        vm.first_approved_30_6_20 = doc.data().sections[1].approved_30_6_20
                        vm.first_approved_20_21 = doc.data().sections[1].approved_20_21
                        vm.first_total_approved_2021 = doc.data().sections[1].total_approved_2021
                    }
                    
                    vm.company_name = doc.data().sections[0].companies[0].company_name
                    vm.outgoing = doc.data().sections[0].companies[0].outgoing
                    vm.execution_rate = doc.data().sections[0].companies[0].execution_rate
                    if (doc.data().sections[0].companies[1]) {
                        vm.company_name_1 = doc.data().sections[0].companies[1].company_name
                        vm.outgoing_1 = doc.data().sections[0].companies[1].outgoing
                        vm.execution_rate_1 = doc.data().sections[0].companies[1].execution_rate
                    }
                    if (doc.data().sections[1].companies[0]) {
                        vm.company_name_2 = doc.data().sections[1].companies[0].company_name
                        vm.outgoing_2 = doc.data().sections[1].companies[0].outgoing
                        vm.execution_rate_2 = doc.data().sections[1].companies[0].execution_rate
                    }
                    if (doc.data().sections[1].companies[1]) {
                        vm.company_name_3 = doc.data().sections[1].companies[1].company_name
                        vm.outgoing_3 = doc.data().sections[1].companies[1].outgoing
                        vm.execution_rate_3 = doc.data().sections[1].companies[1].execution_rate                        
                    }
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('projects').where('project_id', '==', this.$route.params.project_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.project_id = doc.data().project_id
                        this.project_desc = doc.data().project_desc
                        this.project_info = doc.data().project_info
                        this.residual = doc.data().residual
                        this.to_start = doc.data().to_start
                        this.to_end = doc.data().to_end
                        this.section_name = doc.data().sections[0].section_name
                        this.contract_value = doc.data().sections[0].contract_value
                        this.approved_30_6_20 = doc.data().sections[0].approved_30_6_20
                        this.approved_20_21 = doc.data().sections[0].approved_20_21
                        this.total_approved_2021 = doc.data().sections[0].total_approved_2021

                        if (doc.data().sections[1]) {
                            this.section_name_1 = doc.data().sections[1].section_name
                            this.contract_value_1 = doc.data().sections[1].contract_value
                            this.first_approved_30_6_20 = doc.data().sections[1].approved_30_6_20
                            this.first_approved_20_21 = doc.data().sections[1].approved_20_21
                            this.first_total_approved_2021 = doc.data().sections[1].total_approved_2021
                        }
                        
                        this.company_name = doc.data().sections[0].companies[0].company_name
                        this.outgoing = doc.data().sections[0].companies[0].outgoing
                        this.execution_rate = doc.data().sections[0].companies[0].execution_rate

                        if (doc.data().secttions[0].companies[1]) {
                            this.company_name_1 = doc.data().sections[0].companies[1].company_name
                            this.outgoing_1 = doc.data().sections[0].companies[1].outgoing
                            this.execution_rate_1 = doc.data().sections[0].companies[1].execution_rate                            
                        }
                        if (doc.data().secttions[1].companies[0]) {
                            this.company_name_2 = doc.data().sections[1].companies[0].company_name
                            this.outgoing_2 = doc.data().sections[1].companies[0].outgoing
                            this.execution_rate_2 = doc.data().sections[1].companies[0].execution_rate
                        }
                        if (doc.data().secttions[1].companies[1]) {
                            this.company_name_3 = doc.data().sections[1].companies[1].company_name
                            this.outgoing_3 = doc.data().sections[1].companies[1].outgoing
                            this.execution_rate_3 = doc.data().sections[1].companies[1].execution_rate
                        }
                    })
            })
        },
        deleteProject() {
            if(confirm('Are you sure?')) {
                db.collection('projects').where('project_id', '==', this.$route.params.project_id).get()
                    .then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            doc.ref.delete()
                            this.$router.push('/')
                        })
                })
            }
        }
    }
}
</script>


